@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

.inicio{
  position: relative;
 
}

.Banner-image{
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-bottom-left-radius: 10px;
}

.Banner {
  height: 100vh;
  width: 80%;
  z-index: 1;
}

.container-banner {
  width: 20%;
  height: 100%;
  
}

.gradient{
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0.9) 5%, transparent);
  opacity: 0.7;
  z-index: 10;
  width: 80%;
  height: 100%;
  position: absolute;
  border-bottom-left-radius: 15px;
}

.main-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.Texts-banner{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-family: 'Poppins', sans-serif;
    position: absolute;
    margin-left: 5vw;
    max-width: 500px;
    z-index: 2;
}

.title-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;

}

.Title-banner{
    max-width: 35rem;
    font-size: 3rem;
    max-height: 60px;
    letter-spacing: 0px;
    color: #414141;
    opacity: 1;
    background: #FFDC00;
    display: inline;
    position: relative;
    color:black;
    font-weight: 600;
    margin: 5px 0;
    padding-right: 12px;
}

.Description-banner{
    max-width: 25rem;
    font-size: 1.2rem;
    height: 100px;
    letter-spacing: 0px;
    line-height: 25px;
    color: #414141;
    opacity: 1;
}

@media(max-width: 700px) {
  .container {
    width: 0;
  }

  .Texts-banner {
    max-width: 80%;
  }

  .Title-banner{
    font-size: 1.8rem;
    max-width: 20rem;
    padding-right: 8px;

  }  
  .Description-banner{
    font-size: 0.9rem;
    max-width: 20rem;  
  }  
  .Banner{  
    width: 100%;
    left: -250px;
  }

  .Banner-image{
    height: 100vh;
    width: 100vw;
  }
}


